              <el-select v-model="format" @change="generatePreview" style="width: 100%">
                <el-option label="PNG" value="png"></el-option>
                <el-option label="JPEG" value="jpeg"></el-option>
                <el-option label="WebP" value="webp"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="ÁîüÊàêÊ†∑Âºè">
              <el-select v-model="style" @change="generatePreview" style="width: 100%">
                <el-option label="Á∫ØËâ≤" value="solid"></el-option>
                <el-option label="Ê∏êÂèò" value="gradient"></el-option>
                <el-option label="Âô™ÁÇπ" value="noise"></el-option>
                <el-option label="Âá†‰ΩïÂõæÂΩ¢" value="geometric"></el-option>
                <el-option label="ÂÉèÁ¥†Ëâ∫ÊúØ" value="pixel"></el-option>
                <el-option label="ÊäΩË±°Ëâ∫ÊúØ" value="abstract"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="È¢úËâ≤Ê®°Âºè">
              <el-select v-model="colorMode" @change="generatePreview" style="width: 100%">
                <el-option label="ÈöèÊú∫Ëâ≤ÂΩ©" value="random"></el-option>
                <el-option label="ÊöñËâ≤Ë∞É" value="warm"></el-option>
                <el-option label="ÂÜ∑Ëâ≤Ë∞É" value="cool"></el-option>
                <el-option label="ÂçïËâ≤Ë∞É" value="monochrome"></el-option>
                <el-option label="ÂΩ©ËôπËâ≤" value="rainbow"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="Â§çÊùÇÂ∫¶">
              <el-slider 
                v-model="complexity" 
                :min="1" 
                :max="10" 
                show-input
                @change="generatePreview"
              ></el-slider>
            </el-form-item>

            <el-form-item label="Ê∑ªÂä†ÊñáÂ≠ó">
              <el-switch v-model="addText" @change="generatePreview"></el-switch>
            </el-form-item>

            <el-form-item v-if="addText" label="ÊñáÂ≠óÂÜÖÂÆπ">
              <el-input 
                v-model="textContent" 
                placeholder="ËæìÂÖ•Ë¶ÅÊòæÁ§∫ÁöÑÊñáÂ≠ó"
                @input="generatePreview"
              ></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="generateImage">
                <i class="el-icon-picture"></i> ÁîüÊàêÂõæÁâá
              </el-button>
              <el-button @click="generateRandom">
                <i class="el-icon-refresh"></i> ÈöèÊú∫ÂèÇÊï∞
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card class="presets-card" style="margin-top: 20px;">
          <div slot="header">
            <span>Â∏∏Áî®Â∞∫ÂØ∏</span>
          </div>
          
          <div class="preset-sizes">
            <el-button 
              v-for="preset in presetSizes" 
              :key="preset.name"
              size="small" 
              @click="applyPreset(preset)"
              class="preset-btn"
            >
              {{ preset.name }}
              <br>
              <small>{{ preset.width }}√ó{{ preset.height }}</small>
            </el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
        <el-card class="preview-card">
          <div slot="header">
            <span>È¢ÑËßà</span>
            <div style="float: right;">
              <el-button size="mini" @click="downloadImage" :disabled="!generatedImage">
                <i class="el-icon-download"></i> ‰∏ãËΩΩ
              </el-button>
              <el-button size="mini" @click="copyImageUrl" :disabled="!generatedImage">
                <i class="el-icon-copy-document"></i> Â§çÂà∂ÈìæÊé•
              </el-button>
            </div>
          </div>
          
          <div class="preview-container">
            <div v-if="!generatedImage" class="no-preview">
              <i class="el-icon-picture-outline"></i>
              <p>ÁÇπÂáª"ÁîüÊàêÂõæÁâá"Êü•ÁúãÈ¢ÑËßà</p>
            </div>
            
            <div v-else class="image-preview">
              <img :src="generatedImage" :alt="`${width}√ó${height} ${format}`" />
              <div class="image-info">
                <div class="info-item">
                  <strong>Â∞∫ÂØ∏:</strong> {{ width }} √ó {{ height }} px
                </div>
                <div class="info-item">
                  <strong>Ê†ºÂºè:</strong> {{ format.toUpperCase() }}
                </div>
                <div class="info-item">
                  <strong>Ê†∑Âºè:</strong> {{ getStyleName() }}
                </div>
                <div class="info-item">
                  <strong>Â§ßÂ∞è:</strong> {{ getImageSize() }}
                </div>
              </div>
            </div>
          </div>
        </el-card>

        <el-card class="history-card" style="margin-top: 20px;">
          <div slot="header">
            <span>ÁîüÊàêÂéÜÂè≤</span>
            <div style="float: right;">
              <el-button size="mini" @click="clearHistory">
                <i class="el-icon-delete"></i> Ê∏ÖÁ©∫
              </el-button>
            </div>
          </div>
          
          <div v-if="history.length === 0" class="no-history">
            <p>ÊöÇÊó†ÁîüÊàêÂéÜÂè≤</p>
          </div>
          
          <div v-else class="history-list">
            <div v-for="(item, index) in history" :key="index" class="history-item">
              <div class="history-preview">
                <img :src="item.image" :alt="item.name" />
              </div>
              <div class="history-info">
                <div class="history-name">{{ item.name }}</div>
                <div class="history-details">{{ item.width }}√ó{{ item.height }} {{ item.format }}</div>
                <div class="history-time">{{ item.time }}</div>
              </div>
              <div class="history-actions">
                <el-button size="mini" @click="downloadHistoryImage(item)">‰∏ãËΩΩ</el-button>
                <el-button size="mini" type="danger" @click="removeFromHistory(index)">Âà†Èô§</el-button>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import DownloadUtil from '@/util/DownloadUtil'
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'RandomImageView',
  metaInfo: {
    title: 'ÈöèÊú∫ÂõæÁâáÁîüÊàêÂô® - Ëá™ÂÆö‰πâÂ∞∫ÂØ∏ÂõæÁâáÁîüÊàêÂ∑•ÂÖ∑',
    meta: [
      { name: 'description', content: 'ÁîüÊàêÊåáÂÆöÂ∞∫ÂØ∏ÂíåÊ†ºÂºèÁöÑÈöèÊú∫ÂõæÁâáÔºåÊîØÊåÅÂ§öÁßçÊ†∑ÂºèÂíåÈ¢úËâ≤Ê®°ÂºèÔºåÈÄÇÁî®‰∫éÂç†‰ΩçÂõæ„ÄÅÊµãËØïÂõæÁâáÁ≠âÂú∫ÊôØ„ÄÇ' },
      { name: 'keywords', content: 'ÈöèÊú∫ÂõæÁâá,ÂõæÁâáÁîüÊàêÂô®,Âç†‰ΩçÂõæ,ÊµãËØïÂõæÁâá,PNG,JPEG,WebP' }
    ]
  },
  data() {
    return {
      width: 400,
      height: 300,
      format: 'png',
      style: 'gradient',
      colorMode: 'random',
      complexity: 5,
      addText: true,
      textContent: '',
      generatedImage: null,
      history: [],
      
      presetSizes: [
        { name: 'Â§¥ÂÉè', width: 100, height: 100 },
        { name: 'Áº©Áï•Âõæ', width: 150, height: 150 },
        { name: 'Ê®™ÂπÖ', width: 728, height: 90 },
        { name: 'ÊñπÂΩ¢', width: 300, height: 300 },
        { name: '16:9', width: 640, height: 360 },
        { name: '4:3', width: 640, height: 480 },
        { name: 'ÂÖ®Â±è', width: 1920, height: 1080 },
        { name: 'ÊâãÊú∫', width: 375, height: 667 }
      ],
      
      styleNames: {
        solid: 'Á∫ØËâ≤',
        gradient: 'Ê∏êÂèò',
        noise: 'Âô™ÁÇπ',
        geometric: 'Âá†‰ΩïÂõæÂΩ¢',
        pixel: 'ÂÉèÁ¥†Ëâ∫ÊúØ',
        abstract: 'ÊäΩË±°Ëâ∫ÊúØ'
      }
    }
  },
  mounted() {
    this.loadHistory()
    this.generatePreview()
  },
  methods: {
    generateImage() {
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      
      canvas.width = this.width
      canvas.height = this.height
      
      // Ê†πÊçÆÊ†∑ÂºèÁîüÊàê‰∏çÂêåÁöÑÂõæÁâá
      switch (this.style) {
        case 'solid':
          this.generateSolid(ctx)
          break
        case 'gradient':
          this.generateGradient(ctx)
          break
        case 'noise':
          this.generateNoise(ctx)
          break
        case 'geometric':
          this.generateGeometric(ctx)
          break
        case 'pixel':
          this.generatePixel(ctx)
          break
        case 'abstract':
          this.generateAbstract(ctx)
          break
      }
      
      // Ê∑ªÂä†ÊñáÂ≠ó
      if (this.addText && this.textContent) {
        this.addTextToCanvas(ctx)
      }
      
      // ËΩ¨Êç¢‰∏∫ÊåáÂÆöÊ†ºÂºè
      const mimeType = `image/${this.format}`
      const quality = this.format === 'jpeg' ? 0.9 : undefined
      this.generatedImage = canvas.toDataURL(mimeType, quality)
      
      // Ê∑ªÂä†Âà∞ÂéÜÂè≤ËÆ∞ÂΩï
      this.addToHistory()
    },
    
    generatePreview() {
      // Âª∂ËøüÁîüÊàêÈ¢ÑËßàÔºåÈÅøÂÖçÈ¢ëÁπÅÊõ¥Êñ∞
      clearTimeout(this.previewTimer)
      this.previewTimer = setTimeout(() => {
        this.generateImage()
      }, 300)
    },
    
    generateSolid(ctx) {
      const color = this.getRandomColor()
      ctx.fillStyle = color
      ctx.fillRect(0, 0, this.width, this.height)
    },
    
    generateGradient(ctx) {
      const gradient = ctx.createLinearGradient(0, 0, this.width, this.height)
      const colors = this.getColorPalette(3)
      
      gradient.addColorStop(0, colors[0])
      gradient.addColorStop(0.5, colors[1])
      gradient.addColorStop(1, colors[2])
      
      ctx.fillStyle = gradient
      ctx.fillRect(0, 0, this.width, this.height)
    },
    
    generateNoise(ctx) {
      const imageData = ctx.createImageData(this.width, this.height)
      const data = imageData.data
      
      for (let i = 0; i < data.length; i += 4) {
        const noise = Math.random() * 255
        data[i] = noise     // Red
        data[i + 1] = noise // Green
        data[i + 2] = noise // Blue
        data[i + 3] = 255   // Alpha
      }
      
      ctx.putImageData(imageData, 0, 0)
    },
    
    generateGeometric(ctx) {
      // ËÉåÊôØËâ≤
      ctx.fillStyle = this.getRandomColor()
      ctx.fillRect(0, 0, this.width, this.height)
      
      // ÁîüÊàêÂá†‰ΩïÂõæÂΩ¢
      const shapes = Math.floor(this.complexity * 2)
      for (let i = 0; i < shapes; i++) {
        ctx.fillStyle = this.getRandomColor()
        ctx.globalAlpha = 0.7
        
        const shapeType = Math.floor(Math.random() * 3)
        if (shapeType === 0) {
          // ÂúÜÂΩ¢
          const x = Math.random() * this.width
          const y = Math.random() * this.height
          const radius = Math.random() * Math.min(this.width, this.height) / 4
          ctx.beginPath()
          ctx.arc(x, y, radius, 0, Math.PI * 2)
          ctx.fill()
        } else if (shapeType === 1) {
          // Áü©ÂΩ¢
          const x = Math.random() * this.width
          const y = Math.random() * this.height
          const w = Math.random() * this.width / 3
          const h = Math.random() * this.height / 3
          ctx.fillRect(x, y, w, h)
        } else {
          // ‰∏âËßíÂΩ¢
          const x1 = Math.random() * this.width
          const y1 = Math.random() * this.height
          const x2 = Math.random() * this.width
          const y2 = Math.random() * this.height
          const x3 = Math.random() * this.width
          const y3 = Math.random() * this.height
          
          ctx.beginPath()
          ctx.moveTo(x1, y1)
          ctx.lineTo(x2, y2)
          ctx.lineTo(x3, y3)
          ctx.closePath()
          ctx.fill()
        }
      }
      
      ctx.globalAlpha = 1
    },
    
    generatePixel(ctx) {
      const pixelSize = Math.max(2, Math.floor(Math.min(this.width, this.height) / 50))
      
      for (let x = 0; x < this.width; x += pixelSize) {
        for (let y = 0; y < this.height; y += pixelSize) {
          ctx.fillStyle = this.getRandomColor()
          ctx.fillRect(x, y, pixelSize, pixelSize)
        }
      }
    },
    
    generateAbstract(ctx) {
      // ËÉåÊôØÊ∏êÂèò
      const gradient = ctx.createRadialGradient(
        this.width / 2, this.height / 2, 0,
        this.width / 2, this.height / 2, Math.max(this.width, this.height) / 2
      )
      const colors = this.getColorPalette(3)
      gradient.addColorStop(0, colors[0])
      gradient.addColorStop(0.5, colors[1])
      gradient.addColorStop(1, colors[2])
      
      ctx.fillStyle = gradient
      ctx.fillRect(0, 0, this.width, this.height)
      
      // Ê∑ªÂä†ÊäΩË±°Á∫øÊù°
      const lines = this.complexity * 5
      for (let i = 0; i < lines; i++) {
        ctx.strokeStyle = this.getRandomColor()
        ctx.lineWidth = Math.random() * 5 + 1
        ctx.globalAlpha = 0.6
        
        ctx.beginPath()
        ctx.moveTo(Math.random() * this.width, Math.random() * this.height)
        
        const points = Math.floor(Math.random() * 5) + 2
        for (let j = 0; j < points; j++) {
          ctx.lineTo(Math.random() * this.width, Math.random() * this.height)
        }
        
        ctx.stroke()
      }
      
      ctx.globalAlpha = 1
    },
    
    addTextToCanvas(ctx) {
      const text = this.textContent || `${this.width}√ó${this.height}`
      const fontSize = Math.max(12, Math.min(this.width, this.height) / 10)
      
      ctx.font = `bold ${fontSize}px Arial`
      ctx.textAlign = 'center'
      ctx.textBaseline = 'middle'
      
      // ÊñáÂ≠óÈò¥ÂΩ±
      ctx.fillStyle = 'rgba(0, 0, 0, 0.5)'
      ctx.fillText(text, this.width / 2 + 2, this.height / 2 + 2)
      
      // ‰∏ªÊñáÂ≠ó
      ctx.fillStyle = 'white'
      ctx.fillText(text, this.width / 2, this.height / 2)
    },
    
    getRandomColor() {
      switch (this.colorMode) {
        case 'warm':
          return `hsl(${Math.random() * 60}, 70%, 60%)`
        case 'cool':
          return `hsl(${180 + Math.random() * 120}, 70%, 60%)`
        case 'monochrome': {
          const gray = Math.floor(Math.random() * 256)
          return `rgb(${gray}, ${gray}, ${gray})`
        }
        case 'rainbow':
          return `hsl(${Math.random() * 360}, 80%, 60%)`
        default:
          return `hsl(${Math.random() * 360}, ${50 + Math.random() * 50}%, ${30 + Math.random() * 40}%)`
      }
    },
    
    getColorPalette(count) {
      const colors = []
      for (let i = 0; i < count; i++) {
        colors.push(this.getRandomColor())
      }
      return colors
    },
    
    generateRandom() {
      this.width = Math.floor(Math.random() * 800) + 200
      this.height = Math.floor(Math.random() * 600) + 200
      this.style = Object.keys(this.styleNames)[Math.floor(Math.random() * Object.keys(this.styleNames).length)]
      this.colorMode = ['random', 'warm', 'cool', 'monochrome', 'rainbow'][Math.floor(Math.random() * 5)]
      this.complexity = Math.floor(Math.random() * 10) + 1
      this.generateImage()
    },
    
    applyPreset(preset) {
      this.width = preset.width
      this.height = preset.height
      this.generatePreview()
    },
    
    downloadImage() {
      if (!this.generatedImage) return
      const filename = `random-image-${this.width}x${this.height}.${this.format}`
      DownloadUtil.downloadImage(this.generatedImage, filename)
    },

    async copyImageUrl() {
      if (!this.generatedImage) return

      try {
        const success = await ClipboardUtil.copyText(this.generatedImage)
        if (success) {
          this.$message.success('ÂõæÁâáÈìæÊé•Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•: ' + error.message)
      }
    },
    
    addToHistory() {
      const historyItem = {
        image: this.generatedImage,
        name: `${this.getStyleName()}-${Date.now()}`,
        width: this.width,
        height: this.height,
        format: this.format,
        style: this.style,
        time: new Date().toLocaleString()
      }
      
      this.history.unshift(historyItem)
      if (this.history.length > 20) {
        this.history = this.history.slice(0, 20)
      }
      
      this.saveHistory()
    },
    
    downloadHistoryImage(item) {
      const link = document.createElement('a')
      link.download = `${item.name}.${item.format}`
      link.href = item.image
      link.click()
    },
    
    removeFromHistory(index) {
      this.history.splice(index, 1)
      this.saveHistory()
    },
    
    clearHistory() {
      this.history = []
      this.saveHistory()
      this.$message.success('ÂéÜÂè≤ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫')
    },
    
    saveHistory() {
      localStorage.setItem('randomImageHistory', JSON.stringify(this.history))
    },
    
    loadHistory() {
      const saved = localStorage.getItem('randomImageHistory')
      if (saved) {
        this.history = JSON.parse(saved)
      }
    },
    
    getStyleName() {
      return this.styleNames[this.style] || this.style
    },
    
    getImageSize() {
      if (!this.generatedImage) return '0 KB'
      
      // ‰º∞ÁÆóÂõæÁâáÂ§ßÂ∞è
      const base64Length = this.generatedImage.length
      const sizeInBytes = (base64Length * 3) / 4
      
      if (sizeInBytes < 1024) {
        return Math.round(sizeInBytes) + ' B'
      } else if (sizeInBytes < 1024 * 1024) {
        return Math.round(sizeInBytes / 1024) + ' KB'
      } else {
        return Math.round(sizeInBytes / (1024 * 1024)) + ' MB'
      }
    }
  }
}
</script>

